<template>
  <v-menu
    offset-y
    :close-on-click="false"
    :close-on-content-click="false"
    allow-overflow
  >
    <template v-slot:activator="{ on, attrs }">
      <v-btn id="viewFind" icon v-bind="attrs" v-on="on">
        <v-icon>mdi-magnify</v-icon>
      </v-btn>
    </template>
    <v-card>
      <v-container>
        <v-row
          justify="space-around"
          align="center"
          class="findbarInputContainer"
          id="findbarInputContainer"
        >
          <v-text-field
            id="findInput"
            placeholder="在文档中查找..."
            v-model="query"
            :counter="counter"
            :counter-value="(_) => this.current"
          />
          <v-btn icon @click="dispatch('again', true)">
            <v-icon>mdi-arrow-up</v-icon>
          </v-btn>
          <v-btn icon @click="dispatch('again')">
            <v-icon>mdi-arrow-down</v-icon>
          </v-btn>
        </v-row>
        <v-row justify="space-around" align="center">
          <v-btn-toggle v-model="toggle_multiple" tile group multiple>
            <v-btn text small @click="updateType('highlightallchange')">
              全部高亮
            </v-btn>
            <v-btn text small @click="updateType('casesensitivitychange')">
              区分大小写
            </v-btn>
            <v-btn text small @click="updateType('diacriticmatchingchange')">
              匹配变音符号
            </v-btn>
            <v-btn text small @click="updateType('entirewordchange')">
              字词匹配
            </v-btn>
          </v-btn-toggle>
        </v-row>
      </v-container>
    </v-card>
  </v-menu>
</template>

<script type="ts">
import Vue from "vue";
import { EVENT, SEARCH_MAP, FIND_STATE } from "@/components/doc";

export default Vue.extend({
  name: "PdfFindBar",

  data: () => ({
    toggle_multiple: [],
    query: "",
    type: "find",
    current: 0,
    total: 0,
  }),

  computed: {
    counter() {
      return this.total ? this.total : false;
    },
  },

  watch: {
    toggle_multiple() {
      this.dispatch(this.type);
    },
    query(val) {
      this.dispatch("");
      if (val == "") {
        this.current = 0;
        this.total = 0;
      }
    },
  },

  methods: {
    dispatch(type, findPrevious = false) {
      const { query } = this;
      const { entireWord, highlightAll, caseSensitive, matchDiacritics } =
        this.getSearchParams();

        console.log('11',{
        source: this,
        type, //
        query, // 查找内容
        phraseSearch: true, // 短句查询  一直为true
        entireWord,  // 字词匹配
        highlightAll, //  全部高亮
        findPrevious, //  是否查找上一项
        caseSensitive, //  区分大小写
        matchDiacritics, // 匹配变音符号
      })
      this.$PDFViewerApplication.eventBus.dispatch("find", {
        source: this,
        type, // 行为模式 findBar的行为都是内部记录的 基于上次的行为进行下一次
        query, // 查找内容
        phraseSearch: true, // 短句查询  一直为true
        entireWord,  // 字词匹配
        highlightAll, //  全部高亮
        findPrevious, //  是否查找上一项
        caseSensitive, //  区分大小写
        matchDiacritics, // 匹配变音符号
      });
    },

    getSearchParams() {
      let params = {
        entireWord: false,
        highlightAll: false,
        caseSensitive: false,
        matchDiacritics: false,
      };

      Object.keys(SEARCH_MAP).map((i) => {
        const index = Number(i);
        if (this.toggle_multiple.includes(index)) {
          const key = SEARCH_MAP[i];
          params[key] = true;
        }
      });

      return params;
    },

    updateType(type) {
      this.type = type;
    },
  },

  mounted() {
    const { eventBus } = this.$PDFViewerApplication;
    eventBus.on(EVENT.PDF.FIND_MATCHES_COUNT, ({ current, total }) => {
      this.current = current;
      this.total = total;
    });

    eventBus.on(EVENT.PDF.UPDATE_FIND_CONTROL_STATE, ({ state }) => {
      if (state == 1) { // FIND_STATE.NOT_FOUND
        this.result = `找不到指定词语`;
      }
    });
  },
});
</script>

<style lang="scss" scoped>
.findbarInputContainer {
  padding: 0 12px;
}
</style>
