// 计算相对于左下角坐标
const leftTopTransformToLeftBottom = (leftTopPosition, pageHeight) => {
  const leftBottomPosition = {
    x: leftTopPosition.x,
    y: pageHeight - leftTopPosition.y,
  };
  return leftBottomPosition;
};

// 计算水印宽度
const computeTextWdith = (text, fontSize, font) => {
  if (font) {
    // 1、使用 Canvas 自带方法获取文字宽度，
    // 这里会考虑字体宽度，因此，同样字数和字号，字体不同，宽度也会不同
    const tempCanvas = document.createElement("canvas");
    const tempCtx = tempCanvas.getContext("2d");
    if (tempCtx) {
      tempCtx.font = fontSize + "px " + font;
      return tempCtx.measureText(text).width;
    } else {
      return fontSize * text.length;
    }
  } else {
    // 2、使用与服务端相同的方案获取文字宽度，
    // 直接用字号乘以字数，无论用什么字体，宽度都是一样的
    return fontSize * text.length;
  }
};

// 转换水印数据
const convertConfig = (config, pageWidth, pageHeight) => {
  const result = {
    color: "",
    font: "",
    fontName: "",
    fontSize: 0,
    fullscreen: "",
    file: "",
    fileWidth: 0,
    fileHeight: 0,
    leftBottomPosition: { x: 0, y: 0 },
    opacity: 0,
    position: { x: 0, y: 0 },
    rotate: 0,
    source: "",
    text: "",
    density: 0,
    width: 0,
    height: 0,
  };

  if (config.file && config.file.length !== 0) {
    result.source = "file";
  } else {
    result.source = "text";
  }

  // 文字水印
  if (result.source === "text") {
    //文字内容
    result.text = config.text.trim();
    result.font = config.fontName;
    result.fontName = config.font;
    result.fontSize = config.size;
    result.color = config.color;
  }

  // 文件水印
  if (result.source === "file") {
    result.file = config.file;
  }

  // 旋转
  result.rotate = Number(config.rotate) % 360;
  if (result.rotate < 0) {
    result.rotate = 360 + result.rotate;
  }

  // 将旋转角度处理成逆时针旋转
  result.rotate = result.rotate !== 0 ? 360 - result.rotate : 0;

  // 不透明度
  result.opacity = config.opacity;

  if (config.isTiled) {
    // 平铺水印
    result.fullscreen = "tile";
    // 密度
    result.density = Number(config.density) / 100;
  } else {
    // 单个水印
    result.fullscreen = "no";
    result.position = { x: 0, y: 0 };

    const padding = 20;

    // 垂直
    if (config.vertical === "top") {
      result.position.y = -pageHeight / 2 + padding;
    } else if (config.vertical === "bottom") {
      result.position.y = pageHeight / 2 - padding;
    } else if (config.vertical === "middle") {
      result.position.y = 0;
    } else {
      result.position.y = Number(config.vertical);
    }

    // 水平
    if (config.horizontal === "left") {
      result.position.x = -pageWidth / 2 + padding;
    } else if (config.horizontal === "right") {
      result.position.x = pageWidth / 2 - padding;
    } else if (config.horizontal === "center") {
      result.position.x = 0;
    } else {
      result.position.x = Number(config.horizontal);
    }

    // 相对于页面左下角
    result.leftBottomPosition = leftTopTransformToLeftBottom(
      {
        x: pageWidth / 2 + result.position.x,
        y: pageHeight / 2 + result.position.y,
      },
      pageHeight
    );
  }

  return result;
};

/**
 * 计算水印在 x，y 轴上投影的长度，按页面原始比例计算
 */
const getDistance = (config) => {
  const result = { x: 0, y: 0 };

  const quadrant = config.rotate;
  const rotationInRadians = (quadrant * Math.PI) / 180;
  let width = 0;
  let height = 0;

  if (config.source === "text") {
    // 计算文字宽度
    const textWidth = computeTextWdith(config.text, config.fontSize);

    width = textWidth / config.density;
    height = config.fontSize / config.density;
  }

  if (config.source === "file") {
    width = config.fileWidth / config.density;
    height = config.fileHeight / config.density;
  }

  // 水印对角线
  const textDiagonal = Math.sqrt(Math.pow(width, 2) + Math.pow(height, 2));
  // 对角线与水印底边的夹角
  const diagonalAngle = Math.atan(height / width);

  // 水平宽度
  let textHorizontalLength = 0;
  // 垂直高度
  let textVerticalHeight = 0;

  if (quadrant === 0 || quadrant === 180) {
    textVerticalHeight = height;
    textHorizontalLength = width;
  } else if (quadrant === 90 || quadrant === 270) {
    textVerticalHeight = width;
    textHorizontalLength = height;
  } else if (0 < quadrant && quadrant < 90) {
    textVerticalHeight =
      textDiagonal * Math.abs(Math.sin(rotationInRadians + diagonalAngle));
    textHorizontalLength =
      textDiagonal * Math.abs(Math.cos(rotationInRadians + diagonalAngle));
  } else if (90 < quadrant && quadrant < 180) {
    textVerticalHeight =
      textDiagonal *
      Math.abs(Math.sin(Math.PI - rotationInRadians + diagonalAngle));
    textHorizontalLength =
      textDiagonal *
      Math.abs(Math.cos(Math.PI - rotationInRadians + diagonalAngle));
  } else if (180 < quadrant && quadrant < 270) {
    textVerticalHeight =
      textDiagonal *
      Math.abs(Math.sin(rotationInRadians - Math.PI + diagonalAngle));
    textHorizontalLength =
      textDiagonal *
      Math.abs(Math.cos(rotationInRadians - Math.PI + diagonalAngle));
  } else if (270 < quadrant && quadrant < 360) {
    textVerticalHeight =
      textDiagonal *
      Math.abs(Math.sin(2 * Math.PI - rotationInRadians + diagonalAngle));
    textHorizontalLength =
      textDiagonal *
      Math.abs(Math.cos(2 * Math.PI - rotationInRadians + diagonalAngle));
  }

  result.x = textHorizontalLength;
  result.y = textVerticalHeight;

  return result;
};

/**
 * 计算页面上所有符合x（y）轴上水印距离的x（y）坐标。
 */
const getXYPositions = (
  pageMidPointX,
  pageMidPointY,
  xDistance,
  yDistance,
  pageWidth,
  pageHeight
) => {
  const xyPositions = { x: [], y: [] };

  let leftXCoordinate = pageMidPointX;
  let rightXCoordinate = pageMidPointX;

  const truth = true;
  while (truth) {
    leftXCoordinate -= xDistance;
    if (leftXCoordinate < 0) {
      break;
    }
    xyPositions.x.push(leftXCoordinate);
  }
  xyPositions.x.push(pageMidPointX);
  while (truth) {
    rightXCoordinate += xDistance;
    if (rightXCoordinate > pageWidth) {
      break;
    }
    xyPositions.x.push(rightXCoordinate);
  }

  let upYCoordinate = pageMidPointY;
  let bottomYCoordinate = pageMidPointY;

  while (truth) {
    upYCoordinate += yDistance;
    if (upYCoordinate > pageHeight) {
      break;
    }
    xyPositions.y.push(upYCoordinate);
  }
  xyPositions.y.push(pageMidPointY);
  while (truth) {
    bottomYCoordinate -= yDistance;
    if (bottomYCoordinate < 0) {
      break;
    }
    xyPositions.y.push(bottomYCoordinate);
  }

  return xyPositions;
};

/**
 * 渲染水印
 */
const renderWaterMark = (
  config,
  targetCanvas,
  targetScale,
  targetWidth,
  targetHeight,
  pageView
) => {
  // 创建一个不可见的 Canvas，先绘制一个水印，之后再与页面显示的 Canvas 合并
  // 否则之后复制、移动、旋转、缩放等操作会影响当前显示的 Canvas
  const tempCanvas = document.createElement("canvas");
  tempCanvas.width = targetWidth;
  tempCanvas.height = targetHeight;
  const tempCtx = tempCanvas.getContext("2d");

  const pageCenterX = tempCanvas.width / 2;
  const pageCenterY = tempCanvas.height / 2;

  if (tempCtx) {
    // 绘制临时水印
    tempCtx.translate(tempCanvas.width / 2, tempCanvas.height / 2);
    tempCtx.rotate((config.rotate * Math.PI) / 180);
    tempCtx.translate(-tempCanvas.width / 2, -tempCanvas.height / 2);

    // 不透明度
    tempCtx.globalAlpha = config.opacity;

    new Promise((resolve) => {
      if (config.source === "text" && config.text) {
        // 设置文字样式
        tempCtx.textAlign = "center";
        tempCtx.fillStyle = config.color;
        tempCtx.font = config.fontSize * targetScale + "px " + config.font;

        // 绘制文字
        tempCtx.fillText(config.text, pageCenterX, pageCenterY);

        resolve();
      } else if (config.source === "file" && config.file) {
        const image = new Image();
        image.onload = function () {
          const imageWidth = image.width * targetScale;
          const imageHeight = image.height * targetScale;
          config.fileWidth = image.width;
          config.fileHeight = image.height;
          tempCtx.drawImage(
            image,
            0,
            0,
            image.width,
            image.height,
            pageCenterX - imageWidth / 2,
            pageCenterY - imageHeight / 2,
            imageWidth,
            imageHeight
          );
          resolve();
        };
        image.src = config.file;
      } else if (pageView) {
        targetCanvas.width = targetWidth;
        targetCanvas.height = targetHeight;
        const targetCtx = targetCanvas.getContext("2d");
        if (targetCtx) {
          targetCtx.drawImage(
            pageView.canvas,
            0,
            0,
            pageView.canvas.width,
            pageView.canvas.height,
            0,
            0,
            targetWidth,
            targetHeight
          );
        }
      }
    }).then(() => {
      const watermarkImg = tempCanvas.toDataURL("png", 1);
      return new Promise((resolve) => {
        const image = new Image();
        image.onload = function () {
          const targetCtx = targetCanvas.getContext("2d");

          if (pageView && targetCtx) {
            targetCanvas.width = targetWidth;
            targetCanvas.height = targetHeight;
            targetCtx.drawImage(
              pageView.canvas,
              0,
              0,
              pageView.canvas.width,
              pageView.canvas.height,
              0,
              0,
              targetWidth,
              targetHeight
            );
          }

          const pageCenterX = targetWidth / 2;
          const pageCenterY = targetHeight / 2;
          const textLayerElements = [];
          if (config.fullscreen === "tile") {
            // 平铺
            const distance = getDistance(config);
            const minXDistance = distance.x;
            const minYDistance = distance.y;
            let yDistance;
            const xDistance = (yDistance =
              minXDistance > minYDistance ? minXDistance : minYDistance);

            const xyPositions = getXYPositions(
              targetWidth / 2,
              targetHeight / 2,
              xDistance * targetScale,
              yDistance * targetScale,
              targetWidth,
              targetHeight
            );
            for (let i = 0; i < xyPositions.x.length; i++) {
              for (let j = 0; j < xyPositions.y.length; j++) {
                // 渲染水印
                targetCtx &&
                  targetCtx.drawImage(
                    image,
                    0,
                    0,
                    image.width,
                    image.height,
                    xyPositions.x[i] - image.width / 2,
                    xyPositions.y[j] - image.height / 2,
                    image.width,
                    image.height
                  );
              }
            }
          } else {
            // 单个水印
            const horizontal = config.position.x * targetScale;
            const vertical = config.position.y * targetScale;

            // 渲染水印
            targetCtx &&
              targetCtx.drawImage(
                image,
                0,
                0,
                image.width,
                image.height,
                pageCenterX - image.width / 2 + horizontal,
                pageCenterY - image.height / 2 + vertical,
                image.width,
                image.height
              );
          }
          resolve();
        };
        image.src = watermarkImg;
      });
    });
  }
};

export { convertConfig, renderWaterMark };
