import * as Services from "@/service";
import { getLocalPassword, saveLocalPassword } from "@/utils/index";
// initial state
const state = () => ({
  /**
   * 印章列表
   * @type {array}
   */
  sealList: [],
  /**
   * 是否打开拖动盖章面板
   * @type {boolearn}
   */
  isStampSeal: false,
  /**
   * 是否展示印章选择框
   * @type {boolearn}
   */
  isSealSelectorVisible: false,
  /**
   * 印章选择框是否展示左右按钮
   * @type {boolearn}
   */
  sealDisabled: false,
});

// getters
const getters = {};

// actions
const actions = {
  // 点击签章按钮
  handleStampSeal({ state, dispatch, commit }) {
    if (window.appConfig.password) {
      dispatch("getSealList", { password: window.appConfig.password });
      return;
    }

    const { password } = getLocalPassword();
    if (password) {
      dispatch("getSealList", { password });
    } else {
      dispatch("dialog/setIsPwdDialogVisible", true, { root: true });
    }
  },
  // 设置 密码弹窗显隐
  setIsStampSeal({ state, commit }, payload) {
    commit("setIsStampSeal", payload);
  },
  setIsSealSelectorVisible({ state, commit }, visible) {
    commit("setIsSealSelectorVisible", visible);
  },
  // 获取印章列表
  getSealList({ state, dispatch, commit }, { password, pwdInfo }) {
    dispatch("dialog/setIsPwdDialogVisible", false, { root: true });
    Services.getSeals(password)
      .then((data) => {
        const param = { password: "", isRemember: false };
        if (pwdInfo?.password) {
          param.password = pwdInfo.password;
          param.isRemember = pwdInfo.isRemember;
        }

        saveLocalPassword(param);

        // 拿到印章列表
        commit("setIsSealSelectorVisible", true);
        commit("setSealList", data.seals);
      })
      .catch((e) => {
        commit("setIsSealSelectorVisible", false);
      });
  },

  setSealDisabled({ state, commit }, sealDisabled) {
    commit("setSealDisabled", sealDisabled);
  },
};

// mutations
const mutations = {
  setSealList(state, sealList) {
    state.sealList = sealList;
  },
  setIsStampSeal(state, isStampSeal) {
    state.isStampSeal = isStampSeal;
  },
  setSealDisabled(state, sealDisabled) {
    state.sealDisabled = sealDisabled;
  },
  setIsPwdDialogVisible(state, isPwdDialogVisible) {
    state.isPwdDialogVisible = isPwdDialogVisible;
  },
  setIsSealSelectorVisible(state, isSealSelectorVisible) {
    state.isSealSelectorVisible = isSealSelectorVisible;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
