
import Vue from "vue";
import RotateController from "@/components/base/RotateController";
import PdfCursorTools from "@/components/base/PdfCursorTools";
import PdfScrollMode from "@/components/base/PdfScrollMode";
import PdfSpreadMode from "@/components/base/PdfSpreadMode";
import EVENT from "@/constants/event";

export default Vue.extend({
  name: "SecondaryToolbar",

  components: {
    RotateController,
    PdfCursorTools,
    PdfScrollMode,
    PdfSpreadMode,
  },

  methods: {
    /**
     * 转到第一页
     */
    firstPage() {
      const { eventBus } = this.$PDFViewerApplication;
      eventBus.dispatch(EVENT.PDF.FIRST_PAGE);
    },

    /**
     * 转到最后一页
     */
    lastPage() {
      const { eventBus } = this.$PDFViewerApplication;
      eventBus.dispatch(EVENT.PDF.LAST_PAGE);
    },
  },
});
