<template>
  <v-app>
    <v-app-bar app :dense="$vuetify.breakpoint.mobile">
      <v-app-bar-nav-icon @click.stop="sidebarToggle" />
      <v-spacer />
      <PageController />
      <v-divider
        :class="$vuetify.breakpoint.mobile ? 'mx-1' : 'mx-4'"
        vertical
      />
      <ScaleController />
      <v-divider
        :class="$vuetify.breakpoint.mobile ? 'mx-1' : 'mx-4'"
        vertical
      />
      <!-- <RotateController /> -->
      <v-spacer />
      <PdfFindBar v-if="!$vuetify.breakpoint.mobile" />
      <WaterMark />
      <!-- <SealSelector
        :seals="sealList"
        :open="isSealSelectorVisible"
        @handleOpen="onSealSelctorBtnClick"
        @close="onSealSelectorClose"
        @cancel="onSealSelectorCancel"
        @confirm="onSealSelectorConfirm"
      /> -->
      <SecondaryToolbar />
    </v-app-bar>

    <v-navigation-drawer v-model="sideBar.visible" absolute left temporary>
      <PdfSidebar />
    </v-navigation-drawer>

    <v-main>
      <ProgressBar />
