<script>
import Vue from "vue";
import PdfFindBar from "@/components/base/PdfFindBar";
import PdfSidebar from "@/components/base/PdfSidebar";
import PageController from "@/components/base/PageController";
import SecondaryToolbar from "@/components/base/SecondaryToolbar";
import ScaleController from "@/components/base/ScaleController";
// import RotateController from "@/components/base/RotateController";
import ProgressBar from "@/components/base/ProgressBar";
import PasswordPrompt from "@/components/base/PasswordPrompt";
import PasswordVerify from "@/components/biz/PasswordVerify";
// import SealSelector from "@/components/biz/SealSelector";
import WaterMark from "@/components/biz/WaterMark";
import EVENT from "@/constants/event";
import { TEXT_LAYER_MODE } from "@/components/doc";
import "./global.scss";

import * as Services from "@/service";

import { mapState } from "vuex";
import { createNamespacedHelpers } from "vuex";
const mapActionsPage = createNamespacedHelpers("page").mapActions;
const mapActionsSeal = createNamespacedHelpers("seal").mapActions;
const mapActionsDialog = createNamespacedHelpers("dialog").mapActions;
const mapActionsPermission = createNamespacedHelpers("permission").mapActions;

export default Vue.extend({
  name: "App",

  components: {
    PdfFindBar,
    PdfSidebar,
    SecondaryToolbar,
    PageController,
    ScaleController,
    // RotateController,
    ProgressBar,
    PasswordPrompt,
    // SealSelector,
    WaterMark,
    PasswordVerify,
  },

  data: () => ({
    password: "123456",
    sideBar: {
      visible: false,
    },
  }),
  computed: {
    ...mapState({
      isPwdDialogVisible: ({ dialog }) => {
        return dialog.isPwdDialogVisible;
      },
      isSealSelectorVisible: ({ seal }) => {
        return seal.isSealSelectorVisible;
      },
      operatorPermission: ({ permission }) => {
        return permission.operatorPermission;
      },
      sealList: ({ seal }) => {
        return seal.sealList;
      },
      currentScale: ({ page }) => {
        return page.currentScale;
      },
      // url: ({ config }) => {
      //   return config.appConfig.url;
      // },
      // documentId: ({ config }) => {
      //   return config.appConfig.documentId;
      // },
      // readOnly: ({ config }) => {
      //   return config.appConfig.readOnly;
      // },
    }),
    textLayerMode() {
      return this.$vuetify.breakpoint.mobile
        ? TEXT_LAYER_MODE.DISABLE
        : TEXT_LAYER_MODE.ENABLE;
    },
    fileUrl() {
      const { url, documentId } = this.$appConfig;
      return `${url}?docType=0&action=download&documentId=${documentId}&t=${new Date().getTime()}`;
    },
  },
  // computed: {
  //   textLayerMode() {
  //     return this.$vuetify.breakpoint.mobile
  //       ? TEXT_LAYER_MODE.DISABLE
  //       : TEXT_LAYER_MODE.ENABLE;
  //   },
  // },

  methods: {
    ...mapActionsSeal([
      "getSealList",
      "setIsSealSelectorVisible",
      "setSealDisabled",
    ]),
    ...mapActionsDialog(["setIsPwdDialogVisible"]),
    ...mapActionsPermission(["setPermission"]),
    ...mapActionsPage(["currentScaleChange", "currentPageNumberChange"]),
    sidebarToggle() {
      const { eventBus } = this.$PDFViewerApplication;

      this.sideBar.visible = !this.sideBar.visible;
      eventBus.dispatch(EVENT.PDF.SIDEBAR.TOGGLE);
      eventBus.dispatch(EVENT.PDF.SIDEBAR.UPDATE_THUMBNAIL_VIEWER);
    },

    //sign
    // 印章按钮点击事件处理
    onSealSelctorBtnClick(type) {
      this.setIsPwdDialogVisible(true);
      this.$refs.signPwd.setType(type);
    },
    /**
     * 密码确认框的处理事件
     * @param type   0 : 盖章   1 ：删章
     */
    onSignPwdSubmit(data) {
      switch (data.type) {
        case 0:
          this.getSealList({ password: data.password, pwdInfo: data });
          // Services.getSeals(password).then((res) => {
          //   // 调用服务或者传参获取印章数据
          //   if (res.status) {
          //     // 获得印章成功 关闭pwd面板 存储密码?
          //     this.setIsPwdDialogVisible(false);
          //     this.sealList = res.seals;
          //     // 打开面板
          //     if (this.sealList.length) {
          //       this.setIsSealSelectorVisible(false);
          //     } else {
          //       // 是否添加提示没有可展示的印章
          //     }
          //   } else {
          //     // 密码错误的提示 或者请求错误
          //   }

          //   // 关闭signPwd
          // });
          break;
        default:
          break;
      }
    },
    // 选择印章弹窗 - close
    onSealSelectorClose() {
      this.setIsSealSelectorVisible(false);
    },
    // 选择印章弹窗 - cancel
    onSealSelectorCancel() {
      this.setIsSealSelectorVisible(false);
    },
    // 选择印章弹窗 - confirm
    onSealSelectorConfirm(data) {
      this.setIsSealSelectorVisible(false);
      const img = document.createElement("img");
      img.width = data.width;
      img.height = data.height;
      img.src = data.imgdata;
      img.onload = () => {
        // this.$refs.setStampSeal.addDragContent({
        //   w: data.width,
        //   h: data.height,
        //   content: img,
        //   ...data,
        // });
        // // 顺序不要变，先添加再显示
        // this.setStampSealVisible = true;
        // this.setSealDisabled(true);
      };
    },
  },
});
</script>
