import {
  createPromiseCapability,
  getDocument,
  GlobalWorkerOptions,
  InvalidPDFException,
  MissingPDFException,
  UnexpectedResponseException,
  version,
  build,
} from "@cloud-reader/pdf.js-dist/legacy/build/pdf";
import {
  EventBus,
  PDFLinkService,
  PDFFindController,
  NullL10n,
  PDFViewer,
  PDFHistory,
  PDFCursorTools,
  CursorTool,
  DEFAULT_SCALE_VALUE,
  SidebarView,
  PDFThumbnailViewer,
  PDFOutlineViewer,
  PDFAttachmentViewer,
  DownloadManager,
  PDFRenderingQueue,
} from "@cloud-reader/pdf.js-dist/legacy/web/pdf_viewer";
import pdfjsWorker from "@cloud-reader/pdf.js-dist/legacy/build/pdf.worker";
import "@cloud-reader/pdf.js-dist/legacy/web/pdf_viewer.css";
import WebViewer from "./WebViewer";
import PasswordPrompt from "./PasswordPrompt.js";
import PDFSidebar from "./PdfSidebar.js";
import EVENT from "@/constants/event";

const DISABLE_AUTO_FETCH_LOADING_BAR_TIMEOUT = 5000; // ms
const ViewOnLoad = {
  UNKNOWN: -1,
  PREVIOUS: 0, // Default value.
  INITIAL: 1,
};

if (typeof window !== "undefined" && "Worker" in window) {
  GlobalWorkerOptions.workerPort = new pdfjsWorker();
}

const PDFViewerApplication = {
  _initializedCapability: createPromiseCapability(),
  appConfig: null,
  pdfDocument: null,
  pdfLoadingTask: null,
  pdfViewer: null,
  pdfHistory: null,
  pdfCursorTools: null,
  eventBus: new EventBus(),
  isViewerEmbedded: window.parent !== window,
  l10n: NullL10n,
  downloadComplete: false,
  defaultUrl: "",
  pdfThumbnailViewer: null,
  pdfSidebar: null,
  pdfOutlineViewer: null,
  pdfAttachmentViewer: null,

  // Called once when the document is loaded.
  async initialize(appConfig) {
    this.appConfig = appConfig;

    this._initializeViewerComponents();

    // Bind the various event handlers *after* the viewer has been
    // initialized, to prevent errors if an event arrives too soon.
    this.bindEvents();

    this._initializedCapability.resolve();
  },

  /**
   * @private
   */
  _initializeViewerComponents() {
    const { appConfig } = this;

    const pdfRenderingQueue = new PDFRenderingQueue();
    pdfRenderingQueue.onIdle = this._cleanup.bind(this);
    this.pdfRenderingQueue = pdfRenderingQueue;

    const linkService = new PDFLinkService({
      eventBus: this.eventBus,
    });
    this.pdfLinkService = linkService;

    const findController = new PDFFindController({
      linkService,
      eventBus: this.eventBus,
    });
    this.findController = findController;

    this.passwordPrompt = new PasswordPrompt({
      eventBus: this.eventBus,
      l10n: this.l10n,
    });

    const container = appConfig.mainContainer,
      viewer = appConfig.viewerContainer;

    const pdfViewer = new PDFViewer({
      container,
      viewer,
      findController,
      eventBus: this.eventBus,
      linkService,
      l10n: this.l10n,
      textLayerMode: appConfig.textLayerMode,
    });

    this.pdfViewer = pdfViewer;
    pdfRenderingQueue.setViewer(pdfViewer);
    linkService.setViewer(pdfViewer);

    const { thumbnailView, outlineView, attachmentsView } = appConfig;
    if (thumbnailView) {
      this.pdfThumbnailViewer = new PDFThumbnailViewer({
        container: thumbnailView,
        eventBus: this.eventBus,
        renderingQueue: pdfRenderingQueue,
        linkService,
        l10n: this.l10n,
      });
      pdfRenderingQueue.setThumbnailViewer(this.pdfThumbnailViewer);
    }

    this.pdfHistory = new PDFHistory({
      eventBus: this.eventBus,
      linkService,
    });
    linkService.setHistory(this.pdfHistory);

    const needSideBar = thumbnailView && outlineView && attachmentsView;
    if (needSideBar) {
      this.pdfSidebar = new PDFSidebar({
        pdfViewer: this.pdfViewer,
        pdfThumbnailViewer: this.pdfThumbnailViewer,
        eventBus: this.eventBus,
        l10n: this.l10n,
      });

      this.pdfSidebar.onToggled = this.forceRendering.bind(this);
    }

    if (outlineView) {
      this.pdfOutlineViewer = new PDFOutlineViewer({
        container: outlineView,
        eventBus: this.eventBus,
        linkService,
      });
    }

    this.downloadManager = new DownloadManager();
    if (attachmentsView) {
      this.pdfAttachmentViewer = new PDFAttachmentViewer({
        container: attachmentsView,
        eventBus: this.eventBus,
        downloadManager: this.downloadManager,
      });
    }

    this.pdfCursorTools = new PDFCursorTools({
      container,
      eventBus: this.eventBus,
      cursorToolOnLoad: CursorTool.SELECT,
    });
  },

  /**
   * @private
   */
  _cleanup() {
    if (!this.pdfDocument) {
      return; // run cleanup when document is loaded
    }
    this.pdfViewer.cleanup();
    this.pdfThumbnailViewer.cleanup();
  },

  run(config) {
    this.initialize(config).then(() => webViewer.initialized());
  },

  get initialized() {
    return this._initializedCapability.settled;
  },

  get initializedPromise() {
    return this._initializedCapability.promise;
  },

  /**
   * 获取文档总页数
   */
  get pagesCount() {
    return this.pdfDocument ? this.pdfDocument.numPages : 0;
  },

  /**
   * 放大
   */
  zoomIn() {
    if (this.pdfViewer.isInPresentationMode) {
      return;
    }
    this.pdfViewer.increaseScale();
  },

  /**
   * 缩小
   */
  zoomOut() {
    if (this.pdfViewer.isInPresentationMode) {
      return;
    }
    this.pdfViewer.decreaseScale();
  },

  /**
   * 获取当前页码
   */
  get page() {
    return this.pdfViewer.currentPageNumber;
  },

  /**
   * 设置并跳转到指定页码
   */
  set page(pageNumber) {
    this.pdfViewer.currentPageNumber = pageNumber;
  },

  /**
   * 关闭已打开的 PDF 文档
   * Closes opened PDF document.
   * @returns {Promise} - Returns the promise, which is resolved when all
   *                      destruction is completed.
   */
  async close() {
    if (!this.pdfLoadingTask) {
      return;
    }

    const promises = [];

    promises.push(this.pdfLoadingTask.destroy());
    this.pdfLoadingTask = null;

    if (this.pdfDocument) {
      this.pdfDocument = null;

      this.pdfThumbnailViewer?.setDocument(null);
      this.pdfViewer.setDocument(null);
      this.pdfLinkService.setDocument(null);
    }

    this.pdfHistory?.reset();
    this.pdfSidebar?.reset();
    this.pdfOutlineViewer?.reset();
    this.pdfAttachmentViewer?.reset();

    await Promise.all(promises);
  },

  /**
   * 通过 URL 或 ArrayBuffer 打开 PDF 文档
   * Opens PDF document specified by URL or array with additional arguments.
   * @param {string|TypedArray|ArrayBuffer} file - PDF location or binary data.
   * @param {Object} [args] - Additional arguments for the getDocument call,
   *                          e.g. HTTP headers ('httpHeaders') or alternative
   *                          data transport ('range').
   * @returns {Promise} - Returns the promise, which is resolved when document
   *                      is opened.
   */
  async open(file, args) {
    if (this.pdfLoadingTask) {
      // We need to destroy already opened document.
      await this.close();
    }

    const parameters = Object.create(null);
    if (typeof file === "string") {
      // URL
      parameters.url = file;
    } else if (file && "byteLength" in file) {
      // ArrayBuffer
      parameters.data = file;
    } else if (file.url && file.originalUrl) {
      parameters.url = file.url;
    }

    // Finally, update the API parameters with the arguments (if they exist).
    if (args) {
      for (const key in args) {
        parameters[key] = args[key];
      }
    }

    const loadingTask = getDocument({
      ...parameters,
      cMapUrl: "./cmaps/",
      cMapPacked: true,
      standardFontDataUrl: "./standard_fonts/",
    });
    this.pdfLoadingTask = loadingTask;

    const { password } = this.appConfig;

    loadingTask.onPassword = (updateCallback, reason) => {
      this.pdfLinkService.externalLinkEnabled = false;
      this.passwordPrompt.setUpdateCallback(updateCallback, reason);

      password
        ? this.passwordPrompt.verify({ password, isCancel: true })
        : this.passwordPrompt.open();

    };

    loadingTask.onProgress = ({ loaded, total }) => {
      this.progress(loaded / total);
    };

    return loadingTask.promise.then(
      (pdfDocument) => {
        this.load(pdfDocument);
      },
      (reason) => {
        if (loadingTask !== this.pdfLoadingTask) {
          return undefined; // Ignore errors for previously opened PDF files.
        }

        let key = "loading_error";
        if (reason instanceof InvalidPDFException) {
          key = "invalid_file_error";
        } else if (reason instanceof MissingPDFException) {
          key = "missing_file_error";
        } else if (reason instanceof UnexpectedResponseException) {
          key = "unexpected_response_error";
        }
        return this.l10n.get(key).then((msg) => {
          this._documentError(msg, { message: reason?.message });
          throw reason;
        });
      }
    );
  },

  /**
   * Show the error box; used for errors affecting loading and/or parsing of
   * the entire PDF document.
   */
  _documentError(message, moreInfo = null) {
    this._otherError(message, moreInfo);

    this.eventBus.dispatch(EVENT.PDF.DOCUMENT_ERROR, {
      source: this,
      message,
      reason: moreInfo?.message ?? null,
    });
  },

  /**
   * Show the error box; used for errors affecting e.g. only a single page.
   *
   * @param {string} message - A message that is human readable.
   * @param {Object} [moreInfo] - Further information about the error that is
   *                              more technical.  Should have a 'message' and
   *                              optionally a 'stack' property.
   */
  _otherError(message, moreInfo = null) {
    const moreInfoText = [
      this.l10n.get("error_version_info", {
        version: version || "?",
        build: build || "?",
      }),
    ];
    if (moreInfo) {
      moreInfoText.push(
        this.l10n.get("error_message", { message: moreInfo.message })
      );
      if (moreInfo.stack) {
        moreInfoText.push(
          this.l10n.get("error_stack", { stack: moreInfo.stack })
        );
      } else {
        if (moreInfo.filename) {
          moreInfoText.push(
            this.l10n.get("error_file", { file: moreInfo.filename })
          );
        }
        if (moreInfo.lineNumber) {
          moreInfoText.push(
            this.l10n.get("error_line", { line: moreInfo.lineNumber })
          );
        }
      }
    }

    Promise.all(moreInfoText).then((parts) => {
      console.error(message + "\n" + parts.join("\n"));
    });
  },

  progress(level) {
    if (this.downloadComplete) {
      // Don't accidentally show the loading bar again when the entire file has
      // already been fetched (only an issue when disableAutoFetch is enabled).
      return;
    }
    const percent = Math.round(level * 100);

    webViewer.updateLoadingBar(percent);

    if (this.disableAutoFetchLoadingBarTimeout) {
      clearTimeout(this.disableAutoFetchLoadingBarTimeout);
      this.disableAutoFetchLoadingBarTimeout = null;
    }
    webViewer.showLoadingBar();

    this.disableAutoFetchLoadingBarTimeout = setTimeout(() => {
      webViewer.hideLoadingBar();
      this.disableAutoFetchLoadingBarTimeout = null;
    }, DISABLE_AUTO_FETCH_LOADING_BAR_TIMEOUT);
  },

  load(pdfDocument) {
    this.pdfDocument = pdfDocument;

    pdfDocument.getDownloadInfo().then(({ length }) => {
      this._contentLength = length; // Ensure that the correct length is used.
      this.downloadComplete = true;
      webViewer.hideLoadingBar();
    });

    const openActionPromise = pdfDocument.getOpenAction().catch(function () {
      /* Avoid breaking initial rendering; ignoring errors. */
    });

    webViewer.setPagesCount({ pagesCount: pdfDocument.numPages });

    this.pdfLinkService.setDocument(pdfDocument);

    const pdfViewer = this.pdfViewer;
    pdfViewer.setDocument(pdfDocument);

    const pdfThumbnailViewer = this.pdfThumbnailViewer;
    pdfThumbnailViewer?.setDocument(pdfDocument);

    const { firstPagePromise, onePageRendered, pagesPromise } = pdfViewer;

    firstPagePromise.then(() => {
      Promise.all([openActionPromise])
        .then(async ([openAction]) => {
          this._initializePdfHistory({
            fingerprint: pdfDocument.fingerprints[0],
            viewOnLoad: 0,
            initialDest: openAction?.dest,
          });

          this.setInitialView();
        })
        .catch(() => {
          this.setInitialView();
        })

    });

    onePageRendered.then(() => {
      pdfDocument.getOutline().then((outline) => {
        if (pdfDocument !== this.pdfDocument) {
          return; // The document was closed while the outline resolved.
        }

        webViewer.subscribeOutline(outline)
        this.pdfOutlineViewer?.render({ outline, pdfDocument });
      });
      pdfDocument.getAttachments().then((attachments) => {
        if (pdfDocument !== this.pdfDocument) {
          return; // The document was closed while the attachments resolved.
        }
        webViewer.subscribeAttachment(attachments)
        this.pdfAttachmentViewer?.render({ attachments });
      });
    });

    pagesPromise.then(() => {
      webViewer.documentLoaded();
    });

    this.pdfHistory.initialize({
      fingerprint: pdfDocument.fingerprints[0],
    });
  },

  /**
   * @private
   */
  _initializePdfHistory({ fingerprint, viewOnLoad, initialDest = null }) {
    if (!this.pdfHistory) {
      return;
    }
    this.pdfHistory.initialize({
      fingerprint,
      resetHistory: viewOnLoad === ViewOnLoad.INITIAL,
      updateUrl: false,
    });

    if (this.pdfHistory.initialBookmark) {
      this.initialBookmark = this.pdfHistory.initialBookmark;

      this.initialRotation = this.pdfHistory.initialRotation;
    }

    // Always let the browser history/document hash take precedence.
    if (
      initialDest &&
      !this.initialBookmark &&
      viewOnLoad === ViewOnLoad.UNKNOWN
    ) {
      this.initialBookmark = JSON.stringify(initialDest);
      // TODO: Re-factor the `PDFHistory` initialization to remove this hack
      // that's currently necessary to prevent weird initial history state.
      this.pdfHistory.push({ explicitDest: initialDest, pageNumber: null });
    }
  },

  setInitialView() {
    if (!this.pdfViewer.currentScaleValue) {
      // Scale was not initialized: invalid bookmark or scale was not specified.
      // Setting the default one.
      this.pdfViewer.currentScaleValue = DEFAULT_SCALE_VALUE;
    }
  },

  forceRendering() {
    this.pdfRenderingQueue.printing = false;
    this.pdfRenderingQueue.isThumbnailViewEnabled =
      this.pdfSidebar.visibleView === SidebarView.THUMBS;

    this.pdfRenderingQueue.renderHighestPriority();
  },

  bindEvents() {
    const { eventBus } = this;
    const {
      FIRST_PAGE,
      LAST_PAGE,
      NEXT_PAGE,
      ZOOM_IN,
      PREVIOUS_PAGE,
      ZOOM_OUT,
      PAGE_NUMBER_CHANGED,
      PAGE_CHANGING,
      SCALE_CHANGED,
      SCALE_CHANGING,
      ROTATE_CHANGING,
      SWITCH_SCROLL_MODE,
      SWITCH_SPREAD_MODE,
      FILE_INPUT_CHANGE,
      UPDATE_FIND_MATCHES_COUNT,
      OUTLINE_BIND_LINK,
      ATTACHMENT_BIND_LINK,
      THUMBNAIL_VIEW_BIND_LINK,
      PAGE_RENDERED,
    } = EVENT.PDF;

    eventBus._on(FIRST_PAGE, (e) => webViewer.firstPage(e));
    eventBus._on(LAST_PAGE, (e) => webViewer.lastPage(e));
    eventBus._on(NEXT_PAGE, (e) => webViewer.nextPage(e));
    eventBus._on(PREVIOUS_PAGE, (e) => webViewer.previousPage(e));
    eventBus._on(ZOOM_IN, (e) => webViewer.zoomIn(e));
    eventBus._on(ZOOM_OUT, (e) => webViewer.zoomOut(e));
    eventBus._on(PAGE_NUMBER_CHANGED, (e) => webViewer.pageNumberChanged(e));
    eventBus._on(SCALE_CHANGED, (e) => webViewer.scaleChanged(e));
    eventBus._on(PAGE_CHANGING, (e) => webViewer.pageChanging(e));
    eventBus._on(SCALE_CHANGING, (e) => webViewer.scaleChanging(e));
    eventBus._on(ROTATE_CHANGING, (e) => webViewer.rotatePage(e));
    eventBus._on(SWITCH_SCROLL_MODE, (e) => webViewer.switchScrollMode(e));
    eventBus._on(SWITCH_SPREAD_MODE, (e) => webViewer.switchSpreadMode(e));
    eventBus._on(FILE_INPUT_CHANGE, (e) => webViewer.fileInputChange(e));
    eventBus._on(UPDATE_FIND_MATCHES_COUNT, (e) =>
      webViewer.updateFindMatchesCount(e)
    );
    eventBus._on(OUTLINE_BIND_LINK, (e) =>
      webViewer.outlineBindLink(e)
    );
    eventBus._on(ATTACHMENT_BIND_LINK, (e) =>
      webViewer.attachmentBindLink(e)
    );
    eventBus._on(THUMBNAIL_VIEW_BIND_LINK, (e) =>
      webViewer.thumbnailViewBindLink(e)
    );
    eventBus._on(PAGE_RENDERED, (e) =>
      webViewer.updateThumbnailView(e)
    );
  },
};

const webViewer = new WebViewer(PDFViewerApplication);
window.PDFViewerApplication = PDFViewerApplication;

export default PDFViewerApplication;
