
import Vue from "vue";
import { EVENT, SCROLL_MODE } from "@/components/doc";

export default Vue.extend({
  name: "PdfScrollMode",

  data: () => ({
    SCROLL_MODE,
  }),

  methods: {
    /**
     * 文档滚动模式
     */
    switchScrollMode(scrollMode: number) {
      const { eventBus } = this.$PDFViewerApplication;
      eventBus.dispatch(EVENT.PDF.SWITCH_SCROLL_MODE, scrollMode);
    },
  },
});
