import EVENT from "@/constants/event";
import { PASSWORD_RESPONSES } from "./constants";
const {
  COMMON: { PASSWORD },
} = EVENT;

class PasswordPrompt {
  updateCallback = null;
  reason = null;

  constructor({ eventBus, l10n }) {
    this.l10n = l10n;
    this.eventBus = eventBus;
    this.verify = this.verify.bind(this);
    this.cancel = this.cancel.bind(this);
    this.invokeCallback = this.invokeCallback.bind(this);

    this.eventBus.on(PASSWORD.VERIFY, this.verify);
    this.eventBus.on(PASSWORD.CANCEL, this.cancel);
  }

  async open() {
    const passwordIncorrect =
      this.reason === PASSWORD_RESPONSES.INCORRECT_PASSWORD;

    const message = await this.l10n.get(
      `password_${passwordIncorrect ? "invalid" : "label"}`
    );

    this.eventBus.dispatch(PASSWORD.OPEN, {
      label: message,
    });
  }

  verify({ password, isCancel = false }) {
    if (password.length) {
      this.invokeCallback(password);
      this.isCancel = isCancel;
    }
  }

  cancel() {
    this.invokeCallback(new Error("PasswordPrompt cancelled."));
  }

  invokeCallback(password) {
    if (!this.updateCallback) {
      return; // Ensure that the callback is only invoked once.
    }
    this.updateCallback(password);
    this.updateCallback = null;
    this.eventBus.dispatch(PASSWORD.PASSED);
  }

  setUpdateCallback(updateCallback, reason) {
    this.updateCallback = updateCallback;
    this.reason = reason;

    this.isCancel && this.cancel();
  }
}

export default PasswordPrompt;
