import { IWatermarkConfig } from "./types";
import { WATERMARK_FONT_OPTIONS } from "../constants/watermark";

const defaultWatermarkConfig = {
  // 水印内容
  text: "金格科技",
  // 水印字体
  font: "黑体",
  // 水印字号
  size: 32,
  // 水印颜色
  color: "rgb(22,22,22)",
  // 旋转角度 0-360
  rotate: 45,
  // 不透明度
  opacity: 80,
  // 是否平铺  true / false
  isTiled: false,
  // 水印密度 0-100
  density: 100,
  // 垂直位置 top / middle / bottom
  vertical: "middle",
  // 水平位置 left / center / right
  horizontal: "center",
};

const setWatermarkConfig = (
  userWatermarkConfig: IWatermarkConfig | null
): IWatermarkConfig | null => {
  if (userWatermarkConfig) {
    const fontName = userWatermarkConfig.font || defaultWatermarkConfig.font;
    return Object.assign({}, defaultWatermarkConfig, userWatermarkConfig, {
      file: null,
      fontName:fontName,
      font: WATERMARK_FONT_OPTIONS.filter((i) => i.text == fontName)[0].text,
      opacity: userWatermarkConfig.opacity / 100,
    });
  } else {
    return null;
  }
};

export { setWatermarkConfig };
