<template>
  <div
    id="appContainer"
    class="appContainer"
    @dragover="onDragover"
    @drop="onDrop"
  >
    <div id="viewerContainer" class="viewerContainer">
      <div id="viewer" class="pdfViewer"></div>
    </div>
    <input type="file" id="fileInput" class="fileInput" />
  </div>
</template>

<script>
import EVENT from "@/constants/event.ts";
import { getBrowserInterfaceSize } from "@/utils";
export default {
  props: {
    url: {
      type: String,
    },
    textLayerMode: {
      type: Number,
    },
    password: {
      type: String,
    },
  },
  methods: {
    onDragover(evt) {
      evt.preventDefault();

      evt.dataTransfer.dropEffect =
        evt.dataTransfer.effectAllowed === "copy" ? "copy" : "move";
    },

    onDrop(evt) {
      evt.preventDefault();

      const { eventBus } = this.$PDFViewerApplication;
      const { files } = evt.dataTransfer;

      if (!files || files.length === 0) {
        return;
      }

      eventBus.dispatch(EVENT.PDF.FILE_INPUT_CHANGE, {
        source: this,
        fileInput: evt.dataTransfer,
      });
    },
  },

  mounted() {
    if (this.url) {
      this.$PDFViewerApplication.defaultUrl = this.url;
    }

    const config = {
      appContainer: document.getElementById("appContainer"),
      mainContainer: document.getElementById("viewerContainer"),
      viewerContainer: document.getElementById("viewer"),
      textLayerMode: this.textLayerMode,
      password: this.password,
      thumbnailView: document.getElementById("thumbnailView"),
      outlineView: document.getElementById("outlineView"),
      attachmentsView: document.getElementById("attachmentsView"),
    };

    this.$PDFViewerApplication.run(config);

    // 移动端计算container的可用高度
    // document.getElementById("viewerContainer").style.height =
    //   getBrowserInterfaceSize().pageHeight - 48 + "px";
  },
};
</script>

<style lang="scss" scoped>
.appContainer {
  height: 100%;
  width: 100%;
  min-height: 800px;
  position: relative;
  background: #ededf0;

  .selected .thumbnailImage {
    border: 2px solid red !important;
  }
  .fileInput {
    display: none;
  }

  .viewerContainer {
    overflow: scroll;
    -webkit-overflow-scrolling: touch;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    box-sizing: content-box;

    height: calc(100vh - 48px);

    &.grab-to-pan-grab {
      cursor: -webkit-grab !important;
      cursor: grab !important;
    }

    &.grab-to-pan-grab
      *:not(input):not(textarea):not(button):not(select):not(:link) {
      cursor: inherit !important;
    }

    &.grab-to-pan-grab:active,
    &.grab-to-pan-grabbing {
      cursor: -webkit-grabbing !important;
      cursor: grabbing !important;
      position: fixed;
      background: rgba(0, 0, 0, 0);
      display: block;
      top: 56px;
      left: 0;
      right: 0;
      bottom: 0;
    }
  }
}
</style>
