import request from "./request";
import { parseQueryString } from "../utils/index";
import { enc } from "crypto-js";

/**
 * 集中式盖章时的印章类型
 * PT：普通印章（OFD 文档不支持普通印章）
 * GM：国密印章
 * GB：国办印章
 */
const sealType = "GM";

/**
 * 印章来源
 * 0：签章服务器
 * 1：硬件密钥盘(客户端)
 **/
const sealOrigin = 0;

/**
 * 证书来源
 * 0：签章服务器
 * 1：硬件密钥盘(客户端)
 */
const certOrigin = 0;

let documentId;
if (window.appConfig.documentId) {
  documentId = window.appConfig.documentId;
} else {
  const queryString = location.search
    ? location.search.split("?")[1]
    : undefined;

  if (queryString) {
    documentId = parseQueryString(queryString).get("documentid");
  }
}

/**
 * 获取文档流
 */
const download = () => {
  if (documentId) {
    return request({
      method: "GET",
      params: { action: "downloadnew", documentId },
      responseType: "arraybuffer",
    });
  } else {
    throw new Error("未指定要加载的文档");
  }
};

/**
 * 获取印章列表
 */
const getSeals = (_password) => {
  const { userId, filterSealNames, password } = window.appConfig;
  return request({
    data: {
      action: "getSeals",
      sealType,
      keySN: userId,
      filterSealNames,
      sealOrigin,
      certOrigin,
      password: password || _password,
    },
  });
};

/**
 * 盖章
 */
const stampSeal = (seals, cert) => {
  const { stampType, userId, documentId } = window.appConfig;
  let convetSeals = seals.map((seal) => ({
    keySN: userId,
    extraPosition: {},
    lastSelected: {},
    currentPage: seal.page.toString(),
    esid: seal.esid,
    width: seal.width,
    height: seal.height,
    imgExt: seal.imgExt,
    imgdata: seal.imgdata,
    left: seal.left,
    top: seal.top,
    page: seal.page.toString(),
    sealData: seal.sealData,
    sealtag: seal.sealtag,
    signName: seal.signName,
    signSN: enc.Base64.stringify(enc.Utf8.parse(seal.signSN)),
    unitName: seal.unitName,
    userCode: seal.userCode,
    userName: seal.userName,
    version: seal.version,
  }));

  convetSeals = stampType != 0 ? [convetSeals] : convetSeals;

  return request({
    data: {
      action: "stampSeal",
      certOrigin,
      documentId,
      stampType,
      cert,
      algorithmType: "RSA",
      sealType,
      appcode: "",
      clientCode: "",
      isUkey: false,
      isWebAuth: false,
      pdfPwd: "",
      seals: convetSeals,
    },
  });
};

/**
 * 验证印章
 */

const verifySeal = ({ pageNo, x, y }) => {
  const { documentId } = window.appConfig;
  return request({
    data: {
      action: "verifySeal",
      documentId,
      certOrigin,
      page: pageNo,
      x,
      y,
      isSM: false,
    },
  });
};

/**
 * 删除印章
 */
const deleteSeal = ({ pageNo, x, y, _password }) => {
  const { password, documentId, userId } = window.appConfig;
  return request({
    data: {
      action: "deleteSeal",
      documentId,
      keySN: userId,
      certOrigin: 0,
      page: pageNo,
      x,
      y,
      password: password || _password,
      isSM: false,
    },
  });
};

/**
 * 判断是不是最后一个章，只有最后一个章才可以删除
 */
const checkLastone = ({ pageNo, x, y }) => {
  const { documentId } = window.appConfig;
  return request({
    data: {
      action: "checkLastone",
      documentId,
      page: pageNo,
      x,
      y,
    },
  });
};

const addWatermark = ({
  color,
  fontName,
  fontSize,
  fullscreen,
  leftBottomPosition,
  opacity,
  rotate,
  source,
  text,
  density,
  file,
}) => {
  return request({
    data: {
      action: "addWatermark",
      documentId: window.appConfig.documentId,
      color,
      fontName,
      file,
      fontSize,
      fullscreen,
      leftBottomPosition,
      opacity,
      density,
      rotate,
      source,
      text,
    },
  });
};

export {
  download,
  getSeals,
  stampSeal,
  deleteSeal,
  checkLastone,
  verifySeal,
  addWatermark,
};
export default {
  download,
  getSeals,
  stampSeal,
  deleteSeal,
  checkLastone,
  verifySeal,
  addWatermark,
};
