
import Vue from "vue";
import { EVENT, SPREAD_MODE } from "@/components/doc";

export default Vue.extend({
  name: "PdfScrollMode",

  data: () => ({
    SPREAD_MODE,
  }),

  methods: {
    /**
     * 文档展示模式
     */
    switchSpreadMode(spreadMode: number) {
      const { eventBus } = this.$PDFViewerApplication;
      eventBus.dispatch(EVENT.PDF.SWITCH_SPREAD_MODE, spreadMode);
    },
  },
});
