// initial state
const state = () => ({
  // 是否打开密码弹窗
  isPwdDialogVisible: false,
  // 是否打开手写面板
  isSignaturePadVisible: false,
});

// getters
const getters = {};

// actions
const actions = {
  setIsPwdDialogVisible({ state, commit }, visible) {
    if (window.appConfig.password) {
      return;
    }
    commit("setIsPwdDialogVisible", visible);
  },
  setIsSignaturePadVisible({ state, commit }, visible) {
    commit("setIsSignaturePadVisible", visible);
  },
};

// mutations
const mutations = {
  setIsPwdDialogVisible(state, visible) {
    state.isPwdDialogVisible = visible;
  },
  setIsSignaturePadVisible(state, visible) {
    state.isSignaturePadVisible = visible;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
