import { AES, enc } from "crypto-js";
import {
  PASSWORD_LOCAL_KEY,
  PASSWORD_LOCAL_TIME,
  PASSWORD_AES_KEY,
} from "@/constants/password";

const { Utf8 } = enc;

const saveLocal = (key: string, value: string, encrypt = false): void => {
  if (encrypt) {
    localStorage.setItem(key, AES.encrypt(value, PASSWORD_AES_KEY).toString());
  } else {
    localStorage.setItem(key, value);
  }
};

const getLocal = (key: string, decrypt = false): string | null => {
  const value = localStorage.getItem(key);

  if (value) {
    if (decrypt) {
      return AES.decrypt(value, PASSWORD_AES_KEY).toString(Utf8);
    } else {
      return value;
    }
  }

  return null;
};

const removeLocal = (key: string): void => {
  localStorage.removeItem(key);
};

const clearLocal = (): void => {
  localStorage.clear();
};

/**
 * localStorage
 */
const getLocalPassword = () => {
  const pwdLocalData = getLocal(PASSWORD_LOCAL_KEY, true);
  if (pwdLocalData) {
    const data = {
      hasTimeOut: false,
      password: "",
      overTime: "",
    };
    const { password, overTime } = JSON.parse(pwdLocalData);
    const timeDiff = Math.abs(overTime - new Date().getTime());
    const hasTimeOut = timeDiff > PASSWORD_LOCAL_TIME;

    if (hasTimeOut) {
      removeLocal(PASSWORD_LOCAL_KEY);
    }

    data.overTime = overTime;
    data.password = password;
    data.hasTimeOut = hasTimeOut;

    return data;
  }

  return { hasTimeOut: true };
};

/**
 * localStorage
 */
type saveType = {
  password: string;
  isRemember: boolean;
};

const saveLocalPassword = (param: saveType): void => {
  const { password, isRemember } = param;

  if (!password.length) {
    return;
  }

  if (isRemember) {
    const overTime = new Date().getTime() + PASSWORD_LOCAL_TIME;
    saveLocal(
      PASSWORD_LOCAL_KEY,
      JSON.stringify({ password: password, overTime }),
      true
    );
  } else if (isRemember) {
    removeLocal(PASSWORD_LOCAL_KEY);
  }
};

export {
  saveLocal,
  getLocal,
  removeLocal,
  clearLocal,
  getLocalPassword,
  saveLocalPassword,
};
