// initial state
const state = () => ({
  appConfig: {
    /**
     * 金格云阅读后端服务接口地址
     * @type {String}
     */
    url: "",

    /**
     * 文档ID
     * 后端盖章、删除、验证时需要通过 documentId 匹配文档
     * @type {String}
     */
    documentId: "",

    /**
     * 是否只读
     * @type {Boolean}
     */
    readOnly: false,
  },
});

// getters
const getters = {};

// actions
const actions = {};

// mutations
const mutations = {
  setDocumentId(state, documentId) {
    state.appConfig.documentId = documentId;
  },
  setUrl(state, url) {
    state.appConfig.url = url;
  },
  setReadOnly(state, readOnly) {
    state.appConfig.readOnly = readOnly;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
