<template>
  <div class="d-flex justify-center align-center">
    <v-btn
      mini
      id="zoomOut"
      icon
      @click="zoomOut"
      :disabled="pageScale <= MIN_SCALE"
    >
      <v-icon>mdi-minus</v-icon>
    </v-btn>

    <v-menu v-show="!$vuetify.breakpoint.mobile">
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          text
          color="black"
          v-bind="attrs"
          v-on="on"
          :class="
            $vuetify.breakpoint.mobile ? 'scaleButton-mobile' : 'scaleButton-pc'
          "
        >
          {{ customScale || selectedScale.text }}
        </v-btn>
      </template>
      <v-list class="scaleList" dense max-height="304">
        <v-subheader>选择缩放比例</v-subheader>
        <v-list-item
          v-for="item in options"
          :key="item.value"
          class="scaleListItem"
          @click="selectScale(item)"
        >
          <v-list-item-title>{{ item.text }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
    <v-btn
      mini
      id="zoomIn"
      icon
      @click="zoomIn"
      :disabled="pageScale >= MAX_SCALE"
    >
      <v-icon>mdi-plus</v-icon>
    </v-btn>
  </div>
</template>

<script>
import Vue from "vue";
import { DEFAULT_SCALE_VALUE, EVENT } from "@/components/doc";

const DEFAULT_SCALE = 1.0;
const MIN_SCALE = 0.1;
const MAX_SCALE = 10.0;
const SCALE_OPTIONS = [
  { text: "自动缩放", value: "auto" },
  { text: "实际大小", value: "page-actual" },
  { text: "适合页面", value: "page-fit" },
  { text: "适合页宽", value: "page-width" },
  { text: "50%", value: 0.5 },
  { text: "75%", value: 0.75 },
  { text: "100%", value: 1 },
  { text: "125%", value: 1.25 },
  { text: "150%", value: 1.5 },
  { text: "200%", value: 2 },
  { text: "300%", value: 3 },
  { text: "400%", value: 4 },
];

export default Vue.extend({
  name: "ScaleController",

  data: () => ({
    options: SCALE_OPTIONS,
    selectedScale: SCALE_OPTIONS[0],
    customScale: "",
    pageScale: DEFAULT_SCALE,
    pageScaleValue: DEFAULT_SCALE_VALUE,
    MIN_SCALE,
    MAX_SCALE,
  }),

  created() {
    const { eventBus } = this.$PDFViewerApplication;
    eventBus.on(EVENT.PDF.SET_PAGE_SCALE, this.setPageScale);
  },

  methods: {
    /**
     * 缩小
     */
    zoomOut() {
      const { eventBus } = this.$PDFViewerApplication;
      eventBus.dispatch(EVENT.PDF.ZOOM_OUT);
    },

    /**
     * 放大
     */
    zoomIn() {
      const { eventBus } = this.$PDFViewerApplication;
      eventBus.dispatch(EVENT.PDF.ZOOM_IN);
    },

    /**
     * 选择缩放比例
     */
    selectScale(item) {
      const { eventBus } = this.$PDFViewerApplication;

      this.selectedScale = item;

      if (item.value) {
        eventBus.dispatch(EVENT.PDF.SCALE_CHANGED, item.value);
      }
    },

    /**
     * 设置缩放比例
     */
    setPageScale({ pageScaleValue, pageScale }) {
      this.pageScale = pageScale;

      if (pageScaleValue) {
        this.pageScaleValue = pageScaleValue;
      } else {
        this.pageScaleValue = Math.round(pageScale * 10000) / 100 + "%";
      }

      let predefinedValueFound = false;

      for (let i = 0; i < SCALE_OPTIONS.length; i++) {
        const option = SCALE_OPTIONS[i];

        if (
          option.value === this.pageScale ||
          option.value === this.pageScaleValue
        ) {
          predefinedValueFound = true;
          this.customScale = option.text;
          this.selectedScale = option;
          break;
        }
      }

      if (!predefinedValueFound) {
        this.customScale = this.pageScaleValue;
        this.selectedScale = {};
      } else {
        this.customScale = "";
      }
    },
  },
});
</script>

<style lang="scss" scoped>
.scaleButton-pc {
  width: 96px;
}
.scaleButton-mobile {
  width: 48px;
}

.mobile-scale {
  width: 70%;
}

default-scale {
}
.scaleList {
  .scaleListItem {
    background: #fff;
  }
}
</style>
