
import Vue from "vue";
import EVENT from "@/constants/event";

export default Vue.extend({
  name: "PageController",

  data: () => ({
    pageNumber: 1,
    oldPageNumber: 1,
    pagesCount: 0,
  }),

  created() {
    const { eventBus } = this.$PDFViewerApplication;
    eventBus.on(EVENT.PDF.SET_PAGE_NUMBER, this.setPageNumber);
    eventBus.on(EVENT.PDF.SET_PAGES_COUNT, this.setPagesCount);
  },

  destroyed() {
    const { eventBus } = this.$PDFViewerApplication;
    eventBus.off(EVENT.PDF.SET_PAGE_NUMBER, this.setPageNumber);
    eventBus.off(EVENT.PDF.SET_PAGES_COUNT, this.setPagesCount);
  },

  methods: {
    /**
     * 设置总页数
     */
    setPagesCount(evt: { pagesCount: number }) {
      this.pagesCount = evt.pagesCount;
    },

    /**
     * 监听页面滚动产生的页码变化
     */
    setPageNumber(evt: { pageNumber: number }) {
      if (evt.pageNumber <= this.pagesCount) {
        this.pageNumber = evt.pageNumber;
        this.oldPageNumber = evt.pageNumber;
      }
    },

    /**
     * 输入页码
     */
    pageNumberChanged(pageNumber: number) {
      const { eventBus } = this.$PDFViewerApplication;

      // 判断页码是否可用的
      if (
        !/^[0-9]{1,20}$/.exec(pageNumber + "") ||
        pageNumber > this.pagesCount
      ) {
        // 页码不可用
        this.pageNumber = this.oldPageNumber;
      } else {
        this.pageNumber = pageNumber;
        this.oldPageNumber = pageNumber;

        eventBus.dispatch(EVENT.PDF.PAGE_NUMBER_CHANGED, pageNumber);
      }
    },

    /**
     * 上一页
     */
    previousPage() {
      const { eventBus } = this.$PDFViewerApplication;
      eventBus.dispatch(EVENT.PDF.PREVIOUS_PAGE);
    },

    /**
     * 下一页
     */
    nextPage() {
      const { eventBus } = this.$PDFViewerApplication;
      eventBus.dispatch(EVENT.PDF.NEXT_PAGE);
    },
  },
});
