
import Vue from "vue";
import EVENT from "@/constants/event";

export default Vue.extend({
  name: "RotateController",

  props: {
    /**
     * 放置位置：toolbar | secondaryToolbar
     */
    placement: {
      type: String,
      default: "toolbar",
    },
  },

  methods: {
    /**
     * 逆时针旋转
     */
    handleRotateCcw() {
      const { eventBus } = this.$PDFViewerApplication;
      eventBus.dispatch(EVENT.PDF.ROTATE_CHANGING, -90);
    },

    /**
     * 顺时针旋转
     */
    handleRotateCw() {
      const { eventBus } = this.$PDFViewerApplication;
      eventBus.dispatch(EVENT.PDF.ROTATE_CHANGING, 90);
    },
  },
});
