<template>
  <v-dialog v-model="visible" persistent max-width="383">
    <v-card>
      <v-card-title> 验证用户密码 </v-card-title>
      <v-form ref="form">
        <v-container fluid style="padding: 24px">
          <v-text-field
            dense
            :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
            :type="show1 ? 'text' : 'password'"
            @click:append="show1 = !show1"
            v-model="passwordDialog.pwd"
            :rules="rules"
          >
          </v-text-field>

          <v-checkbox
            hide-details="false"
            v-model="passwordDialog.remember"
            label="记住密码, 7天内不再显示"
          >
          </v-checkbox>
        </v-container>
      </v-form>
      <v-card-actions>
        <v-spacer />
        <v-btn color="error" @click="cancel">取消</v-btn>
        <v-btn color="primary" dark @click="submit" :loading="loading"
          >确认</v-btn
        >
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script type="ts">
import Vue from "vue";
import EVENT from "@/constants/event";
export default Vue.extend({
  name: "password-verify",
  props: {
    visible: {
      type: Boolean,
      default: false,
    }
  },
  data() {
    return {
      show1: false,
      loading: false,
      passwordDialog: {
        pwd: "",
        remember: false,
      },
      rules: [(v) => !!v || "密码不能为空"],
      type: null,
    };
  },
  watch: {
    visible(v) {
      if (!v) {
        this.loading = false
      }
    }
  },
  methods: {
    setType(type) {
      this.type = type
    },
    cancel() {
      this.$emit('hideSignPwd')
      this.reset()
    },
    submit() {
      if (this.$refs.form.validate()) {
        this.loading = true
        // 校验密码
        // 执行对应的需要用到密码参数的请求操作
        this.$emit('handleSubmit', { type: this.type, password: this.passwordDialog.pwd, isRemember: this.passwordDialog.remember })

        this.reset();
      }
    },
    reset() {
      this.passwordDialog.pwd = ''
        this.passwordDialog.remember = false
    }
  },
  mounted() {
    // const { eventBus } = this.$PDFViewerApplication;
    // eventBus.on(EVENT.SEAL.PASSWORD.OPEN, ({ callback }) => {
    //   this.callback = callback;
    //   this.open()
    // });
  },
});
</script>

