
import Vue from "vue";
import { SIDEBAR_VIEW, SIDEBAR_VIEW_MAP } from "../../doc/PdfViewer/constants";
import EVENT from "@/constants/event";

export default Vue.extend({
  name: "PdfSidebar",

  props: {},

  data: () => ({
    action: "预览",
    SIDEBAR_VIEW: SIDEBAR_VIEW,
    active: SIDEBAR_VIEW.THUMBS,
    viewOutline: document.getElementById("viewOutline"),
    viewThumbnail: document.getElementById("viewThumbnail"),
    viewAttachments: document.getElementById("viewAttachments"),
  }),

  methods: {
    switchView(view: number) {
      this.action = SIDEBAR_VIEW_MAP[view];
    },
    onTreeLoaded(view: number) {
      if (this.active === view) {
        this.switchView(SIDEBAR_VIEW.THUMBS);
      }
    },
    outlineloaded() {
      this.onTreeLoaded(SIDEBAR_VIEW.OUTLINE);
    },
    attachmentsloaded() {
      this.onTreeLoaded(SIDEBAR_VIEW.ATTACHMENTS);
    },
    mounted() {
      const { eventBus } = this.$PDFViewerApplication;

      eventBus.on(EVENT.PDF.OUTLINE_LOADED, this.outlineloaded);
      eventBus.on(EVENT.PDF.ATTACHMENTS_LOADED, this.attachmentsloaded);
    },
    destroyed() {
      const { eventBus } = this.$PDFViewerApplication;

      eventBus.off(EVENT.PDF.OUTLINE_LOADED, this.outlineloaded);
      eventBus.off(EVENT.PDF.ATTACHMENTS_LOADED, this.attachmentsloaded);
    },
  },
});
