import QueryString from "qs";
import axios from "axios";
import appConfig from "../config/index.ts";
// import { onError } from "../utils/index";
// import { Loading } from "@design/esign-ui-vue";

let loading;

const request = axios.create({
  baseURL: appConfig.url,
  method: "POST",
  params: { docType: 0 },
  paramsSerializer: (params) =>
    QueryString.stringify(params, { arrayFormat: "brackets" }),
  headers: {
    "Content-Type": "application/json;charset=UTF-8",
    "X-Requested-With": "XMLHttpRequest",
  },
});

request.interceptors.request.use(
  (config) => {
    // loading = Loading.service({
    //   fullscreen: true,
    //   lock: true,
    //   text: "正在加载中",
    // });

    if (config.method === "post") {
      config.data = {
        isEncrypt: false,
        data: config.data,
      };
    }

    return config;
  },
  (error) => {
    return error;
  }
);

request.interceptors.response.use((response) => {
  // loading.close();
  let message;
  switch (response.status) {
    case 200:
      // eslint-disable-next-line no-case-declarations
      const { data } = response;
      if (data) {
        if (
          response.config.responseType === "arraybuffer" &&
          data.byteLength > 0
        ) {
          return data;
        }
        // 因为 getLiscenseInfo 返回的数据中没有 status, || data.companyName 是为了做兼容这种情况
        if (data.status || data.companyName) {
          return data;
        } else if (data.message) {
          // onError(data.message)
          alert(data.message);
          return Promise.reject();
        } else {
          return data.status;
        }
      } else {
        message = `请求失败：${data.status}，请联系管理员`;
        // onError(message)
        alert(data.message);
        return Promise.reject();
      }
    default:
      message = `请求失败：${data.status}，请联系管理员`;
      // onError(message)
      alert(data.message);
      return Promise.reject();
  }
});

export default request;
