<template>
  <v-dialog v-model="dialog" persistent max-width="400">
    <v-card>
      <v-card-title class="text-h6">文档已加密</v-card-title>
      <v-card-text>
        <v-text-field
          v-model="password"
          :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
          :type="showPassword ? 'text' : 'password'"
          :label="label"
          @click:append="showPassword = !showPassword"
        />
      </v-card-text>
      <v-card-actions>
        <v-spacer />
        <v-btn text @click="cancel">取消</v-btn>
        <v-btn text @click="submit">确认</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script type="ts">
import EVENT from "@/constants/event";
import Vue from "vue";

export default Vue.extend({
  name: "PasswordPrompt",
  data() {
    return {
      dialog: false,
      showPassword: false,
      password: "",
      label: "请输入密码以打开此 PDF 文档",
    };
  },

  methods: {
    submit() {
      const { eventBus } = this.$PDFViewerApplication;
      const password = this.password;
      // 校验密码
      eventBus.dispatch(EVENT.COMMON.PASSWORD.VERIFY, {
        password,
      });
    },
    cancel() {
      const { eventBus } = this.$PDFViewerApplication;
      eventBus.dispatch(EVENT.COMMON.PASSWORD.CANCEL);
      this.passed();
    },
    open({ label }) {
      // 目前没有做多语言功能，label返回的是'';
      // this.label = label;
      this.dialog = true;
    },
    close() {
      this.dialog = false;
    },
    reset() {
      this.showPassword = false;
      this.password = "";
    },
    passed() {
      this.reset();
      this.close();
    },
  },
  mounted() {
    const { eventBus } = this.$PDFViewerApplication;

    eventBus.on(EVENT.COMMON.PASSWORD.OPEN, this.open);
    eventBus.on(EVENT.COMMON.PASSWORD.PASSED, this.passed);
  },
  destroyed() {
    const { eventBus } = this.$PDFViewerApplication;

    eventBus.off(EVENT.COMMON.PASSWORD.OPEN, this.open);
    eventBus.off(EVENT.COMMON.PASSWORD.PASSED, this.passed);
  },
});
</script>
