
import Vue from "vue";
import { CURSOR_TOOL } from "@/components/doc";

export default Vue.extend({
  name: "PdfCursorTools",

  methods: {
    /**
     * 文本选择工具
     */
    cursorSelectTool() {
      const { pdfCursorTools } = this.$PDFViewerApplication;
      pdfCursorTools.switchTool(CURSOR_TOOL.SELECT);
    },

    /**
     * 手型工具
     */
    cursorHandTool() {
      const { pdfCursorTools } = this.$PDFViewerApplication;
      pdfCursorTools.switchTool(CURSOR_TOOL.HAND);
    },
  },
});
