import PdfViewer, {
  SPREAD_MODE,
  CURSOR_TOOL,
  SEARCH_MAP,
  SCROLL_MODE,
  DEFAULT_SCALE_VALUE,
  TEXT_LAYER_MODE,
  FIND_STATE,
} from "./PdfViewer";
import OfdViewer from "./OfdViewer";
import ImageViewer from "./ImageViewer";
import EVENT from "@/constants/event";

const components = [PdfViewer, OfdViewer, ImageViewer];

const install = (Vue) => {
  components.forEach((component) => {
    Vue.use(component);
  });
};

export {
  EVENT,
  SPREAD_MODE,
  CURSOR_TOOL,
  SEARCH_MAP,
  SCROLL_MODE,
  DEFAULT_SCALE_VALUE,
  TEXT_LAYER_MODE,
  FIND_STATE,
};
export default {
  install,
};
