<template>
  <v-dialog v-model="dialog" persistent max-width="990">
    <template v-slot:activator="{ attrs }">
      <v-btn icon v-bind="attrs" @click="open" v-show="false">
        <v-icon> mdi-watermark </v-icon>
      </v-btn>
    </template>
    <v-card>
      <v-card-title class="text-h5"> 添加水印 </v-card-title>
      <v-card-text>
        <v-form ref="form">
          <v-container>
            <v-row justify="space-between" align="start">
              <v-col cols="3">
                <v-row>
                  <v-file-input
                    accept="image/*"
                    prepend-inner-icon="mdi-image"
                    prepend-icon=""
                    v-model="file"
                    truncate-length="15"
                    label="上传图片作为水印"
                  ></v-file-input>
                </v-row>
                <v-row>
                  <v-text-field
                    :disabled="!isText"
                    v-model="watermark.text"
                    clearable
                    label="文本水印"
                    :rules="textMode"
                  >
                  </v-text-field>
                </v-row>
                <v-row>
                  <v-select
                    :disabled="!isText"
                    v-model="watermark.font"
                    :items="fontNames"
                    label="文本字体"
                    required
                  >
                  </v-select>
                </v-row>
                <v-row>
                  <v-text-field
                    :disabled="!isText"
                    v-model="watermark.size"
                    :rules="isNumber"
                    label="文本字号"
                    type="number"
                    required
                  >
                  </v-text-field>
                </v-row>
                <v-row>
                  <v-color-picker
                    ref="colorPicker"
                    :disabled="!isText"
                    style="width: 100%"
                    v-model="watermark.color"
                    hide-mode-switch
                    hide-inputs
                    mode:sync="rgba"
                    canvas-height="100"
                  ></v-color-picker>
                </v-row>
              </v-col>

              <v-col cols="3">
                <v-row>
                  <v-combobox
                    label="旋转角度"
                    clearable
                    v-model="watermark.rotate"
                    :items="rotateItems"
                    :suffix="watermark.rotate?.text ? '' : '°'"
                    item-text="text"
                    item-value="value"
                    @change="(v) => (this.watermark.rotate = v)"
                    :rules="notNULL"
                  >
                  </v-combobox>
                </v-row>
                <v-row>
                  <v-text-field
                    label="不透明度"
                    v-model="watermark.opacity"
                    suffix="%"
                    :rules="notNULL"
                  >
                  </v-text-field>
                </v-row>
                <v-row>
                  <v-checkbox
                    v-model="watermark.isTiled"
                    label="是否平铺"
                  ></v-checkbox>
                </v-row>
                <v-row>
                  <v-text-field
                    :disabled="!watermark.isTiled"
                    v-model="watermark.density"
                    label="水印密度"
                    :rules="notNULL"
                  >
                  </v-text-field>
                </v-row>
                <v-row>
                  <v-combobox
                    :disabled="watermark.isTiled"
                    label="垂直位置"
                    clearable
                    v-model="watermark.vertical"
                    :items="verticalItems"
                    :suffix="watermark.vertical?.text ? '' : 'px'"
                    item-text="text"
                    item-value="value"
                    @change="(v) => (this.watermark.vertical = v)"
                    :rules="notNULL"
                  >
                  </v-combobox>
                </v-row>
                <v-row>
                  <v-combobox
                    :disabled="watermark.isTiled"
                    label="水平位置"
                    clearable
                    v-model="watermark.horizontal"
                    :items="horizontalItems"
                    :suffix="watermark.horizontal?.text ? '' : 'px'"
                    item-text="text"
                    item-value="value"
                    @change="(v) => (this.watermark.horizontal = v)"
                    :rules="notNULL"
                  >
                  </v-combobox>
                </v-row>
              </v-col>
              <v-col cols="5" style="height: 100%">
                <v-card style="width: 100%; height: 476px; padding: 0">
                  <v-row justify="center" align="center" style="height: 100%">
                    <canvas id="previewCanvas"></canvas>
                  </v-row>
                </v-card>
              </v-col>
            </v-row>
          </v-container>
        </v-form>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color=" darken-1" text @click="cancel"> 取消 </v-btn>
        <v-btn color="primary" @click="submit" style="margin-right: 24px">
          确认
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import Vue from "vue";
import EVENT from "@/constants/event";
import {
  WATERMARK_FONT_OPTIONS,
  WATERMARK_ROTATE_OPTIONS,
  WATERMARK_VERTICAL_OPTIONS,
  WATERMARK_HORIZONTAL_OPTIONS,
} from "@/constants/watermark";
import { convertConfig, renderWaterMark } from "@/utils";

export default Vue.extend({
  name: "WaterMark",

  data: () => ({
    fontNames: WATERMARK_FONT_OPTIONS,
    dialog: false,
    isText: true,
    rotateItems: WATERMARK_ROTATE_OPTIONS,
    verticalItems: WATERMARK_VERTICAL_OPTIONS,
    horizontalItems: WATERMARK_HORIZONTAL_OPTIONS,
    file: null,
    watermark: {
      file: null,
      // 水印内容
      text: "金格科技",
      // // 水印字体  黑体/楷体/宋体/微软雅黑
      font: WATERMARK_FONT_OPTIONS[0].value,
      // // 水印字号
      size: 32,
      // 水印颜色 rgb格式
      color: "rgb(0,255,0)",
      // 旋转角度 0-360
      rotate: WATERMARK_ROTATE_OPTIONS[0],
      // 不透明度
      opacity: 100,
      // 是否平铺  true / false
      isTiled: true,
      // 水印密度  0-100
      density: 100,
      // 垂直位置  top / middle / bottom
      vertical: WATERMARK_VERTICAL_OPTIONS[0],
      // 水平位置  left / center / right
      horizontal: WATERMARK_HORIZONTAL_OPTIONS[0],
    },
    textMode: [(v) => !!v || "水印内容不能为空"],
    notNULL: [(v) => !!v || "不能为空"],
    isNumber: [
      (v) => {
        const pattern = /^\+?[1-9][0-9]*$/;
        if (pattern.test(v)) {
          return true;
        }
        return "not number";
      },
    ],
  }),
  watch: {
    async file(val) {
      if (val) {
        this.isText = false;
        // 不传空 跳过rules
        if (!this.watermark.text) this.watermark.text = "   ";
        await this.getImageBase64(val);
      } else {
        this.isText = true;
        this.watermark.file = null;
      }
    },
    watermark: {
      handler(val) {
        if (this.dialog && this.$refs.form) {
          if (this.$refs.form.validate()) {
            this.renderWaterMark();
            this.isSubmit = true;
          } else {
            this.isSubmit = false;
          }
        }
      },
      deep: true,
    },
  },
  methods: {
    documentRenderWatermark(config) {
      const { pdfViewer, pagesCount } = this.$PDFViewerApplication;
      for (let i = 0; i < pagesCount; i++) {
        const pageView = pdfViewer.getPageView(i);
        const pageScale = pageView.viewport.scale;
        const pageWidth = pageView.width / pageScale;
        const pageHeight = pageView.height / pageScale;
        const watermark = convertConfig(config, pageWidth, pageHeight);

        pageView.cloudReader_watermark_add(watermark);
      }
    },
    addWatermark() {
      const config = this.getOutput();
      this.documentRenderWatermark(config);
    },
    previewWatermark(config) {
      if (!config) {
        return;
      }
      this.documentRenderWatermark(config);
    },
    renderWaterMark() {
      this.$nextTick(() => {
        const { pdfViewer, page } = this.$PDFViewerApplication;
        const pageView = pdfViewer.getPageView(page - 1);
        const pageScale = pageView.viewport.scale;
        const pageWidht = pageView.width / pageScale;
        const pageHeight = pageView.height / pageScale;

        // 将第一页绘制在 this.previewCanvas 上，绘制比例为原页面大小的一半
        const previewScale = 0.5;
        const previewWidth = pageWidht * previewScale;
        const previewHeight = pageHeight * previewScale;

        let data = this.getOutput();
        const config = convertConfig(data, pageWidht, pageHeight); // 获取水印设置
        const previewCanvas = document.querySelector("#previewCanvas");
        renderWaterMark(
          config,
          previewCanvas,
          previewScale,
          previewWidth,
          previewHeight,
          pageView,
          false
        );
        // 渲染水印
        // if (this.isSubmit) {
        //   转换数据给后端
        //   const params = this.convertParams();
        //   this.$service.addWatermark(params).then(() => {
        //     this.addWatermark();
        //   });
        // }
      });
    },

    /**
     * 获得可用的watermark-config对象
     */
    getOutput() {
      let output = {
        ...this.watermark,
      };
      output.vertical = output.vertical?.text
        ? output.vertical.value
        : output.vertical;

      output.rotate = output.rotate?.text ? output.rotate.value : output.rotate;

      output.horizontal = output.horizontal?.text
        ? output.horizontal.value
        : output.horizontal;

      output.opacity = output.opacity / 100;

      output.fontName = WATERMARK_FONT_OPTIONS.filter(
        (i) => i.value == output.font
      )[0].text;

      if (this.$refs.colorPicker) {
        let v = this.$refs.colorPicker.$data.internalValue;
        output.color = `rgb(${v.rgba["r"]},${v.rgba["g"]},${v.rgba["b"]})`;
      }
      return output;
    },

    /**
     * 打开水印面板
     */
    open() {
      // color-picker无法接受rgb字符串格式 要手动转换值
      if (typeof this.watermark.color === "string") {
        let colorCopy = this.watermark.color
          .replace("rgb(", "")
          .replace(")", "")
          .split(",");
        this.watermark.color = {
          b: colorCopy[2],
          g: colorCopy[1],
          r: colorCopy[0],
        };
      }
      this.dialog = true;
      this.renderWaterMark();
    },
    cancel() {
      this.dialog = false;
    },
    submit() {
      if (this.$refs.form.validate()) {
        // TEST  打印参数
        console.log(this.getOutput());
        this.renderWaterMark();
      }
    },

    /**
     * 获取图片文件base64
     * @param {*} file
     */
    async getImageBase64(file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        let fileBase64 = e.target.result;
        this.watermark.file = fileBase64;
      };
      reader.readAsDataURL(file);
    },
  },
  mounted() {
    const { eventBus } = this.$PDFViewerApplication;

    const { previewWatermark } = this.$appConfig;

    eventBus.on(EVENT.PDF.DOCUMENT_LOADED, () =>
      this.previewWatermark(previewWatermark)
    );
  },
  destroyed() {
    const { eventBus } = this.$PDFViewerApplication;
    eventBus.off(EVENT.PDF.DOCUMENT_LOADED, this.addWatermark);
  },
});
</script>

<style lang="scss" scoped></style>
