import PdfViewer from "./PdfViewer.vue";
import PDFViewerApplication from "./app";
import EVENT from "@/constants/event";

import {
  SPREAD_MODE,
  CURSOR_TOOL,
  SEARCH_MAP,
  SCROLL_MODE,
  DEFAULT_SCALE_VALUE,
  TEXT_LAYER_MODE,
  FIND_STATE,
} from "./constants";

PdfViewer.install = (Vue) => {
  Vue.component("PdfViewer", PdfViewer);
  Vue.prototype.$PDFViewerApplication = PDFViewerApplication;
};

const { PDF } = EVENT;

export {
  PDF,
  SPREAD_MODE,
  CURSOR_TOOL,
  SEARCH_MAP,
  SCROLL_MODE,
  DEFAULT_SCALE_VALUE,
  TEXT_LAYER_MODE,
  FIND_STATE,
};
